body {
  margin: 0;
  font-family: "Comfortaa", cursive;
  background-color: #eff1f2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header h1 {
  margin: 0;
}

.project-main {
  background-color: #dbdede;
  margin: 1rem;
  border-radius: 0.5rem;
  border: 1rem !important;
  max-width: 35vw;
  border-color: black !important;
  padding: 15px !important;
}

.breakdown-main {
  background-color: #dbdede;
  margin: 1rem;
  border-radius: 0.5rem;
  display: none;
  padding: 15px !important;
}

.input-group-text {
  width: -webkit-fill-available;
}

.projectHead {
  display: flex;
  padding: 0 0.5rem;
  justify-content: space-between;
  background-color: #cdcdcd;
  border-radius: 0.3rem;
  flex-wrap: wrap;
  align-items: center;
}
.projectHeadElements {
  margin: 0.5rem;
}

.projectHeadTitle {
  display: flex;
  align-items: baseline;
}

.projectHeadTitle h2 {
  margin: 0.5rem;
}

.projectBody {
  margin: 2rem;
}
.projectBody h3 {
  overflow-wrap: anywhere;
  align-self: center;
  margin: 0.5rem 0 0.5rem 1rem;
}
.projectTitle {
  background-color: #cdcdcd;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.3rem;
}
.projectTitleText {
  width: 26vw;
}

.projectTitle button {
  margin-right: 1rem;
}

.projectTitle button:hover {
  display: initial;
  margin-right: 1rem;
}

.taskHead {
  display: flex;
  padding: 0 0.5rem;
  justify-content: space-between;
  background-color: #cdcdcd;
  border-radius: 0.3rem;
  flex-wrap: wrap;
  align-items: center;
}
.taskHead h3 {
  overflow-wrap: anywhere;
}

.taskHeadElements {
  margin: 0.5rem;
}

.taskBody {
  margin: 2rem;
}
.taskBody h3 {
  margin: 1rem 0;
}
.taskBody h5,
h4,
p {
  overflow-wrap: anywhere;
}
#inlineCheckbox1 {
  margin-left: 0.5rem;
}

.taskHeading {
  display: flex;
  justify-content: space-between;
}

.taskHeading p {
  margin-bottom: 0 !important;
}
.taskItem {
  background-color: #cdcdcd;
  border-radius: 0.3rem;
  margin: 1rem 0;
}

.taskItemInternal {
  padding: 0.3rem 1rem;
}

.titleInput p {
  text-align: end;
  margin-right: 0.5rem;
  color: red;
  margin-top: 0.2rem;
}

.modal-dialog {
  width: 35vw !important;
  margin-top: 20vh !important;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 35vw !important;
    margin: 1.75rem auto;
  }
}

@media (width: 576px) {
  .container {
    max-width: 90vw !important;
  }
}
